<template>
  <div class="texture-generator">
    <h2>🎨 CSS 渐变纹理生成器</h2>

    <!-- 纹理类型 -->
    <label>
      纹理类型：
      <select v-model="selectedPattern">
        <option value="stripe">斜条纹</option>
        <option value="grid">网格</option>
        <option value="dot">圆点</option>
        <option value="brick">砖墙</option>
        <option value="line-horizontal">水平细线</option>
        <option value="line-vertical">垂直细线</option>
      </select>
    </label>

    <!-- 基础参数 -->
    <label>
      颜色：
      <input type="color" v-model="color" />
    </label>

    <label v-if="['stripe', 'line-horizontal', 'line-vertical'].includes(selectedPattern)">
      角度：
      <input type="number" v-model.number="angle" /> deg
    </label>

    <label>
      间距：
      <input type="number" v-model.number="spacing" /> px
    </label>

    <label v-if="['stripe', 'line-horizontal', 'line-vertical'].includes(selectedPattern)">
      线宽：
      <input type="number" v-model.number="lineWidth" /> px
    </label>

    <!-- 预览区域 -->
    <div
        class="preview"
        :style="{
        backgroundImage: generatedGradient,
        backgroundSize: spacing + 'px ' + spacing + 'px'
      }"
    ></div>

    <!-- 生成的 CSS -->
    <div class="code-output">
      <pre>{{ generatedCSS }}</pre>
    </div>
  </div>
</template>

<script setup>
import { ref, computed } from 'vue'

const selectedPattern = ref('stripe')
const color = ref('#ffffff') // 不带透明度，防止颜色选择器异常
const angle = ref(45)
const spacing = ref(20)
const lineWidth = ref(1)

// 计算渐变值
const generatedGradient = computed(() => {
  const dir = `${angle.value}deg`
  const c = color.value
  const w = lineWidth.value
  const s = spacing.value

  switch (selectedPattern.value) {
    case 'stripe':
      return `linear-gradient(${dir},
        ${c} ${w}px,
        transparent ${w}px,
        transparent ${s / 2}px,
        ${c} ${s / 2}px
      )`

    case 'grid':
      return `linear-gradient(to right, ${c} ${w}px, transparent ${w}px),
              linear-gradient(to bottom, ${c} ${w}px, transparent ${w}px)`

    case 'dot':
      return `radial-gradient(circle, ${c} ${w}px, transparent ${w}px, transparent ${s}px, ${c} ${s}px)`

    case 'brick':
      return `linear-gradient(to right, ${c} ${w}px, transparent ${w}px),
              linear-gradient(to bottom, ${c} ${w}px, transparent ${w}px)`

    case 'line-horizontal':
      return `linear-gradient(to right, ${c} ${w}px, transparent ${w}px)`

    case 'line-vertical':
      return `linear-gradient(to bottom, ${c} ${w}px, transparent ${w}px)`

    default:
      return ''
  }
})

// 生成最终 CSS 代码用于复制
const generatedCSS = computed(() => {
  return `.your-class {\n  background-image: ${generatedGradient.value};\n  background-size: ${spacing.value}px ${spacing.value}px;\n}`
})
</script>

<style scoped>
.texture-generator {
  max-width: 600px;
  margin: 2rem auto;
  padding: 1rem;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-family: sans-serif;
}

label {
  display: block;
  margin-bottom: 1rem;
}

.preview {
  width: 100%;
  height: 150px;
  margin-top: 1rem;
  border: 1px solid #999;
  background-repeat: repeat;
}

.code-output {
  margin-top: 1rem;
  white-space: pre-wrap;
  background-color: #f4f4f4;
  padding: 1rem;
  border-radius: 4px;
}
</style>