<script>
export default {
  name: 'ThemeToggle',
  data() {
    return {
      isDarkMode: false
    };
  },
  mounted() {
    this.$nextTick(()=>{
      this.isDarkMode = document.body.classList.contains('dark-mode');
    });
  },
  methods: {
    toggleMode() {
      this.isDarkMode = !this.isDarkMode;
      document.body.classList.toggle('dark-mode', this.isDarkMode);

      // 可选：保存用户偏好到 localStorage
      localStorage.setItem('darkMode', this.isDarkMode);
    }
  }
};
</script>

<style scoped>
.theme-toggle {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  width: 20px;
  height: 20px;
}

.theme-toggle:hover {
  transform: scale(1.1);
}
.icon-sun{
  transform: scale(1.5);
}
</style>