<template>
  <div class="markdown-body" v-html="htmlContent"></div>
</template>
<script>
import {marked} from 'marked'
import markedKatex from 'marked-katex-extension'
import hljs from 'highlight.js'
import "../assets/css/gitub-markdown.css";
import 'highlight.js/styles/github-dark.css';
import "../assets/css/katex.css";
import { pinyin } from 'pinyin-pro';

hljs.registerLanguage('java', require('highlight.js/lib/languages/java'))
hljs.registerLanguage('python', require('highlight.js/lib/languages/python'))
hljs.registerLanguage('javascript', require('highlight.js/lib/languages/javascript'))
hljs.registerLanguage('html', require('highlight.js/lib/languages/xml'))
hljs.registerLanguage('css', require('highlight.js/lib/languages/css'))
// 其他语言依此类推


function slugify(text) {
  return text
      .normalize('NFKC')                   // 统一字符格式
      .toLowerCase()                       // 转小写
      .replace(/[\u4e00-\u9fa5]/g, char => pinyin(char)) // 中文转拼音
      .replace(/[^a-z\u4e00-\u9fa5]/g, '-') // 非字母和中文都替换为 -
      .replace(/-+/g, '-')                  // 多个连续 - 合并成一个 -
      .replace(/^-+|-+$/g, '')              // 去除首尾的 -
}
// 自定义 renderer 处理图片路径
const renderer = new marked.Renderer();
// 修改图片渲染逻辑
renderer.image = function (href, title, text) {
  // 如果不是网络路径，则加上 base 路径或 public 目录前缀
  let finalHref = href.href;
  if (!finalHref.startsWith('http')) {
    // 假设你把图片放在 public/images/ 下
    finalHref = `/images/${finalHref.replace(/^\.\/images\//, '')}`;
  }

  return `<img src="${finalHref}" alt="${text}" ${title ? `title="${title}"` : ''} />`;
};
// 自定义 heading
renderer.heading = function (text) {
  // 去除标点符号并替换空格为 -
  const id = slugify(text.text)
  return `<h${text.depth} id="${id}">${text.text.replace(/\*+/g,'')}</h${text.depth}>`;
};
// 自定义 link，处理锚点跳转
renderer.link = (payload) => {
  const href = payload.href;
  const text = payload.text;
  const title = payload.title || '';
  // 判断是否是锚点链接（以 # 开头）
  if (href && href.startsWith('#')) {
    // 返回一个带 data-anchor 属性的 a 标签
    return `<a href="#${slugify(href)}" data-anchor>${text}</a>`
  }
  // 其他普通链接按默认方式处理
  return `<a href="${href}"${title ? ` title="${title}"` : ''} target="_blank">${text}</a>`
}
marked.use(markedKatex({
  output: 'mathml',   // 👈 改为只输出 MathML
  strict: false,
  throwOnError: false
}))
marked.setOptions({
  highlight: (code, lang) => {
    if (!lang) return hljs.highlight(code, {language: 'plaintext'}).value
    try {
      return hljs.highlight(code, {language: lang}).value
    } catch (e) {
      console.warn(`Highlight.js 无法识别语言：${lang}，已自动降级为 plaintext`)
      return hljs.highlight(code, {language: 'plaintext'}).value
    }
  },
  renderer: renderer,
  gfm: true,
  breaks: false,
  pedantic: false,
  langPrefix: 'language-', // 必须设置，否则 class 名不对

})
export default {
  name: 'MarkdownRender',
  props: {
    content: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      htmlContent: '',
    };
  },
  computed: {
    // 这里也可以用 computed 来替代 setup 中的 marked 处理
  },
  watch: {
    content: {
      immediate: true,
      handler(newVal) {
        this.parseMarkdown(newVal);
        // 在 DOM 加载完成后绑定点击事件
        this.$nextTick(() => {
          // 确保 DOM 渲染完成后再绑定事件
          document.querySelectorAll('a[data-anchor]').forEach(link => {
            link.removeEventListener('click', this.handleAnchorClick); // 避免重复绑定
            link.addEventListener('click', this.handleAnchorClick);
          });
        });
      },
    },
  },
  mounted() {
    this.parseMarkdown(this.content);
    this.$nextTick(() => {
      this.highlightCode();
    });
  },
  methods: {
    handleAnchorClick(e) {
      e.preventDefault();
      const targetId = e.target.getAttribute('href');
      const targetElement = document.querySelector(targetId);
      if (targetElement) {
        targetElement.scrollIntoView({behavior: 'smooth'});
        history.pushState(null, '', targetId);
      }
    },
    parseMarkdown(content) {
      this.htmlContent = marked.parse(content);
      this.$nextTick(() => {
        this.highlightCode();
      });
    },
    highlightCode() {
      document.querySelectorAll('pre code').forEach((block) => {
        hljs.highlightElement(block);
      });
    },
  },
};
</script>

<style scoped>
/* 保持与之前一致即可 */
</style>