<template>
  <div class="nav-section bg-body text-secondary border-gray">
    <!-- 返回按钮 -->
    <div v-if="stack.length > 1" class="back-button" @click="goBack">
      ← 返回上一级
    </div>

    <!-- 当前层级标题 -->
    <h4 v-if="currentLevelTitle" class="current-title">
      {{ currentLevelTitle }}
    </h4>

    <!-- 当前层级内容 -->
    <ul class="section-nav">
      <li
          v-for="section in currentSections"
          :key="section.id"
          class="nav-item"
      >
        <a
            v-if="section.children && section.children.length"
            href="javascript:void(0)"
            class="nav-link has-child hover-primary"
            :class="{ active: activeId === section.id }"
            @click="enterSubLevel(section)"
        >
          {{ section.title }}
        </a>
        <a
            v-else
            href="javascript:void(0)"
            class="nav-link hover-primary"
            :class="{ active: activeId === section.id }"
            @click="markActive(section)"
        >
          {{ section.title }}
        </a>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'NavSection',
  props: {
    sections: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      stack: [],         // 路径栈
      activeId: null     // 当前激活项
    };
  },
  computed: {
    currentSections() {
      return this.stack.length ? this.stack[this.stack.length - 1].children : this.sections;
    },
    currentLevelTitle() {
      if (this.stack.length === 0) return '课程目录';
      return this.stack[this.stack.length - 1].title;
    }
  },
  watch: {
    // ✅ 监听 sections 变化，自动重置 stack
    sections: {
      immediate: true,
      handler(newSections) {
        this.stack = [{ title: '课程目录', children: newSections }];
        this.activeId = null;
      }
    }
  },
  mounted() {
    // 初始压入根级
    this.stack = [{ title: '课程目录', children: this.sections }];
  },
  methods: {
    enterSubLevel(section) {
      this.stack.push(section);
      this.activeId = section.id;
    },
    goBack() {
      this.stack.pop();
      if (this.stack.length === 0) {
        this.stack = [{ title: '课程目录', children: this.sections }];
      }
    },
    markActive(section) {
      this.activeId = section.id;
      this.$emit('handleNavigation', section);
    }
  }
};
</script>
<style scoped>
.nav-section {
  max-width: 200px;
  padding: 1rem 0 0 1rem;
}

.back-button {
  margin-bottom: 0.5rem;
  cursor: pointer;
  font-weight: bold;
}

.current-title {
  padding-right: 1em;
  font-size: 1.1rem;
  font-weight: bold;
}

.section-nav {
  list-style: none;
  padding-left: 0;
  margin: 0;
}

.nav-link {
  display: block;
  text-decoration: none;
  font-size: 0.95rem;
  padding: 0.4rem 0.4rem;
  transition: all 0.3s ease;
}


.nav-link.has-child::after {
  content: '→';
}
.nav-link.active {
  background-color: var(--primary-color);
  color: var(--color-white);
}
@media screen and (max-width: 768px) {
  .nav-section{
    width: 100%;
    max-width: 100vw;
  }

}
</style>