      <div class="left-door"></div>
      <div class="right-door"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "FigureFrame",
  emits: ['clickImg'],
  props: {
    imgUrl: {
      type: String,
      default:require('../assets/image/owner.jpg')
    }
  },
  methods: {

  },
  mounted() {
    console.log(this.imgUrl);
  }
}
