<style scoped>
.door {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  position: relative;
  background-size: contain;
  border-top: 3px solid #6b4423;
  border-left: 3px solid #6b4423;
  border-right: 3px solid #6b4423;
  overflow: hidden;
}

.frame-image {
  width: 100%;
  height: auto;
  pointer-events: none;
}

.left-door,
.right-door {
  width: 50%;
  height: 100%;
  position: absolute;
  top: 0;

  /* 背景颜色 + 纹理 */
  background-color: #8b4513; /* 深棕色木纹底色 */
  background-image:
      linear-gradient(90deg, rgba(255, 255, 255, 0.05) 50%, transparent 50%),
      linear-gradient(0deg, rgba(0, 0, 0, 0.2) 25%, transparent 25%);
  background-size: 40px 40px, 20px 20px;
  background-blend-mode: overlay;

  /* 边框与细节 */
  border: 2px solid #5c3d2e;
  box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.5), 0 10px 20px rgba(0, 0, 0, 0.5);

  /* 门板铆钉效果 */
  background-repeat: repeat;
  transition: all 1.5s ease;
}

.left-door {
  transform-origin: 0 100% 0;
  left: 0;
  top: 0;
}

.right-door {
  transform-origin: 100% 100% 0;
  right: 0;
  top: 0;
}

.door:hover .left-door {
  transform: skewY(-15deg) rotateY(-70deg);
}

.door:hover .right-door {
  transform: skewY(15deg) rotateY(70deg);
}

/* 门把手 */
.left-door::after {
  content: '';
  position: absolute;
  top: 50%;
  right: 10px;
  width: 10px;
  height: 60px;
  background-color: #d2af80;
  border-radius: 5px;
  transform: translateY(-50%);
}

.right-door::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 10px;
  width: 10px;
  height: 60px;
  background-color: #d2af80;
