<template>
  <div class="course-detail-container  ">
      <CollapsibleSection  ref="siderbar" direction="up" class="sidebar-container " :is-show-arrow="sidebarArrowShow">
        <nav class="sidebar ">
        <NavSection :sections="sections" @handleNavigation="handleNavigation" />
        </nav>
      </CollapsibleSection>
    <main ref="content" class="content-container ">
      <MarkdownRender :content="markdownContent" />
    </main>
  </div>
</template>

<script>
import CollapsibleSection from '@/components/CollapsibleSection'
import NavSection from '@/components/NavSection'
import MarkdownRender from "@/components/MarkdownRender";

export default {
  name: 'CourseDetail',
  components: {
    MarkdownRender,
    NavSection,
    CollapsibleSection
  },
  data() {
    return {
      sections: [],
      markdownContent: '',
      sidebarArrowShow:window.innerWidth <= 768
    }
  },
  watch: {
    '$route.params.courseDir': {
      immediate: true,
      handler(courseDir) {
        if (courseDir) {
          this.loadCourseData(courseDir)
        }
      }
    }
  },
  methods: {
    async loadCourseData(courseDir) {

      try {
        const courseJson = require(`@/assets/md/${courseDir}/course.json`)
        this.sections = courseJson;
        const firstFile = this.getFirstLeaf(courseJson)
        if (firstFile) {
          await this.loadMarkdownContent(courseDir, firstFile.id)
        }
      } catch (err) {
        this.markdownContent = '<h2>找不到该课程的内容</h2>';
        this.sections = [];
      }
    },
    getFirstLeaf(nodes) {
      for (const node of nodes) {
        if (!node.children || node.children.length === 0) {
          return node
        } else {
          const result = this.getFirstLeaf(node.children)
          if (result) return result
        }
      }
      return null
    },
    async loadMarkdownContent(courseDir, fileName) {
      try {
        const content = await import(`@/assets/md/${courseDir}/content/${fileName}`)
        this.markdownContent = content.default || content
      } catch (err) {
        this.markdownContent = `<h2>无法加载 ${fileName}</h2>`
      }
    },
    async handleNavigation(item) {
      await  this.loadMarkdownContent(this.$route.params.courseDir, item.id);
      this.$nextTick(() => {
        setTimeout(() => {
          this.$refs.content.scrollTo({
            top: 0,
            behavior: 'smooth' // 可选平滑滚动
          });
        }, 50);
      });
    },
    handleResize() {
      if (window.innerWidth <= 768) {
        this.sidebarArrowShow = true;
      }else{
        this.sidebarArrowShow = false;
        this.$refs.siderbar.open();
      }
    },
  },
  beforeMount() {
    window.addEventListener('resize', this.handleResize);
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.handleResize);
  }
}
</script>
