<template>
  <div class="pagination-container">
    <!-- 分页容器 -->
    <div class="pages">
      <div
          v-for="(page, index) in pages"
          :key="index"
          class="page"
          :class="{ active: index === currentPage }"
          v-html="page"
      ></div>
    </div>

    <!-- 翻页按钮 -->
    <div class="nav">
      <button @click="prevPage" :disabled="currentPage === 0">上一页</button>
      <span>第 {{ currentPage + 1 }} 页 / 共 {{ pages.length }} 页</span>
      <button @click="nextPage" :disabled="currentPage === pages.length - 1">下一页</button>
    </div>
  </div>
</template>

<script>
import htmlContent from '@/assets/html/0.html';

export default {
  name: 'PaginatedContentView',
  props: {
    htmlContent: {
      type: String,
      default: () => htmlContent
    }
  },
  data() {
    return {
      pages: [],
      currentPage: 0,
      resizeTimeout: null,
      headerHTML: '', // 存储原始 head 内容
      scripts: []     // 存储原始脚本地址
    };
  },
  methods: {
    reflowPages() {
      const parser = new DOMParser();
      const doc = parser.parseFromString(this.htmlContent, 'text/html');

      // 保存原始 head 中的样式和 meta 等内容
      this.headerHTML = Array.from(doc.head.children)
          .map(el => el.outerHTML)
          .join('\n');

      // 保存原始 body 的所有子节点
      const container = document.createElement('div');
      container.innerHTML = doc.body.innerHTML;
      const children = Array.from(container.children);

      // 保存原始 <script> 标签 src 地址
      this.scripts = Array.from(doc.scripts).map(script => script.src);

      let currentHeight = 0;
      let pageHTML = '';
      const pageHeight = window.innerHeight - 150;

      for (const node of children) {
        const temp = node.cloneNode(true);
        temp.style.visibility = 'hidden';
        temp.style.position = 'absolute';
        document.body.appendChild(temp);
        const nodeHeight = temp.offsetHeight;
        document.body.removeChild(temp);

        if (currentHeight + nodeHeight > pageHeight && pageHTML !== '') {
          // 新建一页，并加上 headerHTML
          this.pages.push(`<head>${this.headerHTML}</head>${pageHTML}`);
          pageHTML = '';
          currentHeight = 0;
        }

        pageHTML += node.outerHTML;
        currentHeight += nodeHeight;
      }

      if (pageHTML !== '') {
        this.pages.push(`<head>${this.headerHTML}</head><body>${pageHTML}</body>`);
      }

      // 加载所有原始脚本（只加载一次）
      this.loadScripts();
    },

    loadScripts() {
      this.scripts.forEach(src => {
        if (!src) return;

        const script = document.createElement('script');
        script.src = src;
        script.async = true;
        document.body.appendChild(script);
      });
    },

    nextPage() {
      if (this.currentPage < this.pages.length - 1) {
        this.currentPage++;
      }
    },
    prevPage() {
      if (this.currentPage > 0) {
        this.currentPage--;
      }
    },
    handleResize() {
      clearTimeout(this.resizeTimeout);
      this.resizeTimeout = setTimeout(() => {
        this.currentPage = 0;
        this.pages = [];
        this.reflowPages();
      }, 300);
    }
  },
  mounted() {
    this.reflowPages();
    window.addEventListener('resize', this.handleResize);
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.handleResize);
  }
};
</script>

<style scoped>
.pagination-container {
  width: 100%;
  overflow: hidden;
}

.pages {
  width: 100%;
}

.page {
  display: none;
  height: 90vh;
  overflow: auto;
  box-sizing: border-box;
  padding: 20px;
}

.page.active {
  display: block;
}

.nav {
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  background: rgba(255, 255, 255, 0.9);
  padding: 10px 20px;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
}
</style>