<template>
  <div class="article-container border-primary">
    <!-- 左侧文章列表 -->
    <CollapsibleSection ref="siderbar" direction="up" class="sidebar-container " :is-show-arrow="sidebarArrowShow">
      <div class="article-list sidebar">
        <h2>文章列表</h2>
        <ul>
          <li
              v-for="(article,index) in articles"
              :key="index"
              :class="{ active: selectedIndex>=0 && selectedIndex===index}"
              class="hover-primary"
              @click="selectArticle(index)"
          >
            {{ article.title }}
          </li>
        </ul>
        <div v-if="articles.length === 0" class="empty-tip">暂无文章</div>
      </div>
    </CollapsibleSection>

    <!-- 右侧文章内容 -->
    <div class="content-container">
      <div v-if="selectedIndex!==-1" style="display: flex;flex-direction: column;height: 100%">
        <MarkdownRender style="flex: 1" :content="articles[selectedIndex].content" />
        <div class="article-meta text-muted">发布于: {{ articles[selectedIndex].date }}</div>
      </div>
      <div v-else class="empty-tip text-muted">
        请选择一篇文章查看详情
      </div>
    </div>
  </div>
</template>

<script>
import MarkdownRender from "@/components/MarkdownRender";
import articles from "@/assets/articles/articles.json"
import CollapsibleSection from "@/components/CollapsibleSection";
export default {
  name: 'ArticlePage',
  components: {
    CollapsibleSection,
    MarkdownRender
  },
  data() {
    return {
      articles: articles,
      selectedIndex:-1,
      sidebarArrowShow:window.innerWidth <= 768
    };
  },
  methods: {
    async selectArticle(index) {
      try {
        if (this.articles[index]?.content===undefined){
          const content= await import(`@/assets/articles/${articles[index].dirName}/article.md`)
          this.articles[index].content=content.default||content
        }
      }catch(err){
        console.log(err)
        this.articles[index].content=`<h2>无法加载 ${articles[index].dirName}/article.md</h2>`
      }
      console.log(`select ${index}`);
      this.selectedIndex=index;

    },
    formatDate(date) {
      return `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}-${String(date.getDate()).padStart(2, '0')}`;
    },
    handleResize() {
      if (window.innerWidth <= 768) {
        this.sidebarArrowShow = true;
      }else{
        this.sidebarArrowShow = false;
        this.$refs.siderbar.open();
      }
    },
  },
  beforeMount() {
    window.addEventListener('resize', this.handleResize);
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.handleResize);
  }
};
</script>

<style scoped>
.article-container {
  display: flex;
  height: var(--content-height-1);
}

.article-list {
  max-width: 350px;
  padding: 20px;
  border-right: 1px solid #e0e0e0;
  overflow-y: auto;
}

.article-list ul {
  list-style-type: none;
  padding: 0;
}

.article-list li {
  padding: 10px 15px;
  cursor: pointer;
  border-radius: 4px;
  margin-bottom: 8px;
}



.article-list li.active {
  background-color: var(--primary-color);
  color: white;
}

.empty-tip {
  text-align: center;
  padding: 30px 0;
}

.article-meta {
  font-size: 14px;
  text-align: center;
}
@media screen and (max-width: 768px) {
  .article-container{
    flex-direction: column-reverse;
  }
  .article-list{
    max-width: 100%;
    border-right: none;
  }
}
</style>