import { createWebHashHistory, createRouter } from 'vue-router'

import HomePage from "@/views/HomePage";
import AboutMe from "@/views/AboutMe";
import LinearGradient from '@/views/LinearGradient'
import CourseNav from "@/components/CourseNav";
import CourseDetail from "@/components/CourseDetail";
import  ChatAi from '@/views/ChatAI'
import PaginatedContentView from "@/components/PaginatedContentView";
import ArticlePage from "@/views/ArticlePage";
const routes = [
    { path: '/', component: HomePage },
    { path: '/about', component: AboutMe },
    { path: '/linear', component: LinearGradient },
    {
        path: '/courses',component: CourseNav,
        children: [
            { path: '/courses/:courseName/:courseDir', component: CourseDetail }
        ]
    },
    {path: '/chat-ai',component: ChatAi},
    {path: '/test',component: PaginatedContentView},
    {path: '/acticle',component: ArticlePage}
]

const router = createRouter({
    history: createWebHashHistory(),
    routes,
})

export default router